import { Component, OnInit } from '@angular/core';
import { AuthenticatorService } from '../../.././services/authenticator.service';
import { ProductService } from '../../.././services/products.service';
import { Router } from '@angular/router';
import { MessageShowService } from '../../.././services/message-show.service';
declare var $;
@Component({
  selector: 'app-slider',
  templateUrl: './slider.component.html',
  styleUrls: ['./slider.component.scss']
})
export class SliderComponent implements OnInit {

  rowColumns: any;
  sizeArr: any[] = [25, 50, 100, 150, 200, 500, 1000];
  pageIndex: number = 1;
  totalRecords: number = 0;
  displayBatchSize: number = 25;
  staticPageData: any = [];
  staticPageDataSouece: any = [];
 // selectedPageId: any = '';
  selectedPageId:boolean=false;
  pageModel: any = {
    name: '',
    content: '',
    order_number: '',
    image: ''
  }

  constructor(
    private httpService: ProductService,
    private auth: AuthenticatorService,
    private router: Router,
    private msgService: MessageShowService
  ) { }

  ngOnInit(): void {
   // this.setTableData();
    this.getStaticPageData();
  }

  getStaticPageData() {
    this.auth.showLoader();
    this.httpService.getMethod('/api/v2/website/slider/institute/' + sessionStorage.getItem('institute_id'), null).subscribe(
      (res: any) => {
        this.auth.hideLoader();
        this.staticPageDataSouece = res.result;
        this.totalRecords = this.staticPageDataSouece.length;
        this.staticPageData = this.getDataFromDataSource(0);
        //this.fetchTableDataByPage(this.pageIndex);
      },
      err => {
        this.auth.hideLoader();
      }
    );
  }

  // pagination functions 

  fetchTableDataByPage(index) {
    this.pageIndex = index;
    let startindex = this.displayBatchSize * (index - 1);
    this.staticPageData = this.getDataFromDataSource(startindex);
  }

  fetchNext() {
    this.pageIndex++;
    this.fetchTableDataByPage(this.pageIndex);
  }

  fetchPrevious() {
    if (this.pageIndex != 1) {
      this.pageIndex--;
      this.fetchTableDataByPage(this.pageIndex);
    }
  }

  getDataFromDataSource(startindex) {
    let t = this.staticPageDataSouece.slice(startindex, startindex + this.displayBatchSize);
    return t;
  }
  updateTableBatchSize(event) {
    this.pageIndex = 1;
    this.displayBatchSize = event;
    this.fetchTableDataByPage(this.pageIndex);
  }

  editRow(object) {
    // console.log(object.data);
    // sessionStorage.setItem('slider_data', JSON.stringify(object.data));
    // this.router.navigate(['view/website-configuration/slider/edit/' + object.data.id])
    $('#myModal').modal('show');
    this.selectedPageId = true;
    this.pageModel.id = object.id;
    //this.category_model.institution_id = object.data.institution_id;
    this.pageModel.name = object.name;
    this.pageModel.content = object.content;
    this.pageModel.image_url = object.image_url;
    console.log( this.pageModel.image_url.image_url)
    $('#addModal').modal('show');
  }

  deleteRow(obj) {
    confirm("Do you want to delete?")
    this.auth.showLoader();
    this.httpService.deleteFile('/prod/api/v2/website/slider/' + obj.id).subscribe(
      (res: any) => {
        this.auth.hideLoader();
        this.msgService.showErrorMessage('success', '', 'Slider Deleted Successfully');
        this.getStaticPageData();
      },
      err => {
        this.auth.hideLoader();
      }
    );
  }
  checkValidation(event) {
    if (event) {
      return true;
    } else if (this.selectedPageId && this.pageModel.image_url != '') {
      return true;
    } 
    else {
      this.msgService.showErrorMessage('error', '', 'Please select Image')
      return false;
    }
  }

  cancel() {
   // this.router.navigate(['view/website-configuration/slider/list']);
   this.pageModel = {
    name: '',
    content: '',
    order_number: '',
    image: ''
  }

  }

  saveData() {
    let file = (<HTMLFormElement>document.getElementById('uploadFileControl')).files[0];
    console.log(file);
    if (this.checkValidation(file)) {
      if (this.pageModel.name != '' && this.pageModel.name != null) {
        if (this.pageModel.order_number != '' && this.pageModel.order_number != null) {
          const formData = new FormData();
          let data: any = {
            institute_id: sessionStorage.getItem('institute_id'),
            name: this.pageModel.name,
            content: this.pageModel.content,
            order_number: this.pageModel.order_number,
            id:this.pageModel.id
          }
          if (this.selectedPageId) {
           // data.id = this.selectedPageId;
            data.image_url = this.pageModel.image_url;
          }
          formData.append('data', JSON.stringify(data));
          if (file) {
            formData.append('file', file);
          }

          let base = this.auth.getBaseUrl();
          let urlPostXlsDocument = this.selectedPageId ? base + "/prod/api/v2/website/slider/update" : base + "/prod/api/v2/website/slider/create";
          let newxhr = new XMLHttpRequest();
          let auths: any = {
            userid: sessionStorage.getItem('userid'),
            userType: sessionStorage.getItem('userType'),
            password: sessionStorage.getItem('password'),
            institution_id: sessionStorage.getItem('institute_id'),
          }
          let Authorization = btoa(auths.userid + "|" + auths.userType + ":" + auths.password + ":" + auths.institution_id);
          this.selectedPageId ? newxhr.open("PUT", urlPostXlsDocument, true) : newxhr.open("POST", urlPostXlsDocument, true);
          newxhr.setRequestHeader("Authorization", Authorization);
          newxhr.setRequestHeader("x-proc-authorization", Authorization);
          newxhr.setRequestHeader("x-prod-inst-id", sessionStorage.getItem('institute_id'));
          newxhr.setRequestHeader("x-prod-user-id", sessionStorage.getItem('userid'));
          newxhr.setRequestHeader("enctype", "multipart/form-data;");
          newxhr.setRequestHeader("Accept", "application/json, text/javascript");
          newxhr.setRequestHeader("Access-Control-Allow-Origin", "*");

          if (!this.auth.isRippleLoad.getValue()) {
            this.auth.showLoader();
            newxhr.onreadystatechange = () => {
              this.auth.hideLoader();
              if (newxhr.readyState == 4) {
                if (newxhr.status >= 200 && newxhr.status < 300) {
                  // this.clearuploadObject();
                  // this.refreshList();
                  let msg = this.selectedPageId ? 'Slider Updated successfully' : 'Slider Added Successfully';
                  this.msgService.showErrorMessage(this.msgService.toastTypes.success, '', msg);
                  this.cancel();
                  $('#myModal').modal('hide');
                  this.getStaticPageData();
                } else {
                  this.msgService.showErrorMessage(this.msgService.toastTypes.error, '', JSON.parse(newxhr.response).message);
                }
              }
            }
            newxhr.send(formData);
          }
        } else {
          this.msgService.showErrorMessage('error', '', 'Please enter order number');
        }
      } else {
        this.msgService.showErrorMessage('error', '', 'Please enter title');
      }
    }
  }

  clearFile() {
    this.pageModel.image_url = '';
  }
  displayBrowe(){
    document.getElementById('uploadFileControl').click();
  }
}
